@import url("@material/linear-progress/dist/mdc.linear-progress.css");

:root {
  --redContinente: #e20714;
}

.continente-credentials-container-modal {
  background-color: white;
  position: absolute;
  z-index: 99;
  top: 100px;
  left: 50%;
  transform: translate(-50%, 0);
  border: 0.5px solid black;
  border-radius: 3px;
  box-shadow: 5px 5px 5px grey;
}

.continente-credentials-container {
  background-color: white;
  width: 100%;
  height: 100%;
}

.continente-credentials-container iframe {
  background-color: white;
  border: none;
  outline: none;
  width: 100%;
  height: 100%;
}

.header {
  position: absolute;
  display: flex;
  justify-content: center;
  color: var(--redContinente);
}

.close-button {
  position: absolute;
  margin-top: 15px;
  right: 10px;
  cursor: pointer;
  width: 32px;
  height: 32px;
  background-color: #f1f3f5;
  display: flex;
  justify-content: center;
  align-items: center;
}

.close-button svg {
  fill: var(--redContinente);
}

.mdc-linear-progress--indeterminate {
  width: 100%;
}

.mdc-linear-progress__bar-inner {
  border-color: var(--redContinente);
}

.mdc-linear-progress__buffer {
  background-color: #e6e6e6;
}
